import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { IRemito } from '../../models/remito';
import { RemitosService } from '../../service/remitos.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { Observable, Subject, debounceTime, finalize, firstValueFrom, map, startWith, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { MatTableDataSource } from '@angular/material/table';
import { DatePipe } from '@angular/common';
import { MY_DATE_FORMATS } from '@shared/constants/date-format';
import { IProveedor } from 'app/modules/administracion/proveedores/models/proveedor';
import { IArticulo } from 'app/modules/stock/articulos/models/articulo';
import { AddArticuloComponent } from '../addArticulo/add-articulo.component';
import { IDeposito } from 'app/modules/stock/depositos/models/deposito';
import { IAlertConfig } from '@shared/models/alert';
import { EmpresaService } from '@shared/service/app/empresa.service';

@Component({
    selector: 'app-save-remitos',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss'],
    providers: [{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }]
})
export class SaveComponent implements OnInit {
    columns = ['articulo', 'codigointerno', 'unidadmedida', 'cantidad', 'acciones'];
    form: FormGroup;
    remito: IRemito;
    title: string;
    user: any;
    proveedores: any[] = [];
    depositos: IDeposito[] = [];
    orden_compras: any[] = [];
    oc_pendientes: any[] = [];
    mov_art_list: any[] = [];
    mov_art_list_deleted: any[] = [];
    maxPalabras: number = 50;
    minDate: Date;
    maxDate: Date;
    filteredDepositos: Observable<IDeposito[]>;
    filteredProveedores: Observable<IProveedor[]>;
    public dataSource: MatTableDataSource<any> = new MatTableDataSource();
    showAlert = false;
    _alertConfig: IAlertConfig;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    @Output() public creadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    @Output() public editadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    isUpdating = false; //flag para suprimir bucle infinito.

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _remitosService: RemitosService,
        private _empresaService: EmpresaService,
        private datePipe: DatePipe,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<SaveComponent>,
        private dialogo: MatDialog,
    ) {
        this.remito = this.data ? this.data : null;
        this.title = this.remito?.idremito ? `Editar Remito ${this.remito.fecha} - ${this.remito.hora}` : `Nuevo Remito`;
        /* this`user = JSON.parse(sessionStorage.getItem('user')) */
        this.user = JSON.parse(localStorage.getItem('user'));
        // Se define minDate como null o cualquier otra fecha lmite que desees permitir hacia atras
        this.minDate = null;
        // Define maxDate como el dia de hoy
        this.maxDate = new Date();
    }

    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });
        
        // Load empresa data
        this._empresaService.getEmpresa();
        this.getItemsOrdenDeCompraPendiente().then(() => {
            this.getProveedores();
        });
        this.newForm();
        this.exist();
        this.subscribeAlert();
        this.subscribeInputs();
    };

    /**
     * Se realizo esta funcion para solucionar el error recursivo de angular 'RangeError'
     */
    subscribeInputs() {
        this.form.get('proveedor').valueChanges.subscribe(value => {
            if (!this.isUpdating) {
                this.seleccionItem(value);
            }
        });

        this.form.get('oc_pendiente').valueChanges.subscribe(value => {
            if (!this.isUpdating) {
                //Debido a que se tuvo que cambiar la logica original del codigo para el renderizado cuando se escuchaban cambios, 
                //se aado esta condicional que modifica la estructura de los datos de la OC segun sea alta o edicion de un remito
                if (this.remito?.idremito) {
                    value = {
                        idordencompra: value,
                    }
                }

                this.seleccionItem(value);
            }
        });

    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
            next: (config: IAlertConfig) => {
                this.showAlert = true;
                this._alertConfig = {
                    ...config,
                    appearance: 'border',
                };
                let time = 2;
                timer(100, 1500)
                    .pipe(
                        finalize(() => {
                            this.showAlert = false;
                            this._alertConfig = null;
                        }),
                        takeWhile(() => time > 0),
                        takeUntil(this._unsubscribeAll),
                        tap(() => (time -= 1))
                    )
                    .subscribe();
            },
        });
    }

    newForm() {
        const [dia, mes, anio] = this.remito ? this.remito.fecha.split('/') : ''; //Se hace esto para poder manejar el formato que se cambio en el render del componente de tabla dinamica.

        if (this.remito) { //S existe el orden de compra, cargo la grilla con sus articulos...
            this.getItems(this.remito.idremito).then((res) => {
                this.mov_art_list = res.map(m => {
                    return {
                        idarticulo: m.idarticulo,
                        cantidad: m.cantidad,
                        idunidadmedida: m.idunidadmedida,
                        descripcion_articulo: m.descripcion_articulo,
                        descripcion_um: m.descripcion_um,
                        codigo_interno: m.codigo_interno_articulo,
                    }
                })

                this.dataSource = new MatTableDataSource(this.mov_art_list);

                // Verifica el estado de habilitacion o deshabilitacion del control OC y Deposito si contiene movimientos o no
                if (this.mov_art_list.length != 0) {
                    this.form.get('proveedor').disable();
                    this.form.get('oc_pendiente').disable();
                    this.form.get('deposito').disable();
                } else {
                    this.form.get('proveedor').enable();
                    this.form.get('oc_pendiente').enable();
                    this.form.get('deposito').enable();
                }
            });
        }

        this.form = new FormGroup({
            fecha: new FormControl((this.remito?.fecha ? this.datePipe.transform(new Date(`${mes}/${dia}/${anio}`), 'yyyy-MM-dd') : this.datePipe.transform(new Date(), 'yyyy-MM-dd')), [Validators.required]),
            hora: new FormControl((this.remito?.hora ? this.remito.hora : this.datePipe.transform(new Date(), 'HH:mm')), [Validators.required]),
            proveedor: new FormControl((this.remito ? this.remito.descripcion_proveedor : ''), [Validators.required]),
            oc_pendiente: new FormControl((this.remito ? this.remito.idordencompra : ''), [Validators.required]),
            deposito: new FormControl((this.remito ? this.remito.descripcion_deposito : ''), Validators.required),
            observaciones: new FormControl((this.remito ? this.remito.observaciones : ''), [Validators.maxLength(this.maxPalabras)]),
        });
    }

    /**
     * Funcion para contar las palabras en las observaciones
    */
    getPalabrasLength(): number {
        const observaciones = this.form.get('observaciones').value;
        return observaciones ? observaciones.length : 0;
    }

    /**
    * Funcion para verificar si se ha alcanzado el lmite de caracteres en las observaciones
    */
    isPalabraMaxLengthReached(): boolean {
        return this.getPalabrasLength() <= this.maxPalabras;
    }

    /**
     * Si existe un stockinicial le mando 'true' a fondos y nada a TM para que me traiga todos los fondos y TMs al editar.
     * Caso contrario, si se va a crear un stockinicial, llamo a los fondos sin enviarle un parametro asi me trae solo los activos y
    * le envio un paramtro a los tipos de stockinicials para que me traiga los activos.
    */
    exist() {
        if (this.remito?.idremito) {
            this.getDepositos(true);
        } else {
            this.getDepositos();
        }
    }

    async getDepositos(option?) {
        const Depositos = await firstValueFrom(this._remitosService.getDepositos(option));
        new Promise(async (resolve) => {
            this.depositos = Depositos.list;
            this.filteredDepositos = this.form.get('deposito').valueChanges.pipe(
                startWith(''),
                map((value) => this._filterDepositos(value)),
            );

            resolve(true);
        })
    };

    private _filterDepositos(deposito: IDeposito): IDeposito[] {
        const filterValue = deposito.toString().toLowerCase();
        return this.depositos.filter(deposito => deposito.descripcion.toLowerCase().includes(filterValue));
    }

    private _filterProveedores(proveedor: IProveedor): IProveedor[] {
        const filterValue = proveedor.toString().toLowerCase();
        return this.proveedores.filter(proveedor => proveedor.RazonSocial.toLowerCase().includes(filterValue));
    }

    async getItemsOrdenDeCompraPendiente(object?: object) {
        const OrdenesDeCompraPendiente = await firstValueFrom(this._remitosService.getItemsOcPendiente(object));
        this.orden_compras = OrdenesDeCompraPendiente.list;

        // Sort the orden_compras by nro_orden_compra numerically
        this.orden_compras.sort((a, b) => {
            const nroA = parseInt(a.numero_oc, 10);
            const nroB = parseInt(b.numero_oc, 10);
            return nroA - nroB;
        });

        new Promise(async (resolve) => {
            //Encapsulo solo las propiedades que quiero utilizar.
            this.proveedores = OrdenesDeCompraPendiente.list.map(p => {
                return {
                    Codigo: p.idproveedor,
                    RazonSocial: p.razon_social
                }
            });


            //Encapsulo solo las propiedades que quiero utilizar.
            this.oc_pendientes = OrdenesDeCompraPendiente.list.map(ocp => {
                return {
                    idordencompra: ocp.idordencompra,
                    fecha_oc: ocp.fecha_oc,
                    numero_oc: ocp.numero_oc,
                    idproveedor: ocp.idproveedor
                }
            });

            //Quito ordenes de compra repetidas.
            this.getOrdenCompras();

            //Esta fraccion de codigo es para filtrar lo que se escriba en el input.
            this.filteredProveedores = this.form.get('proveedor').valueChanges.pipe(
                startWith(''),
                map((value) => this._filterProveedores(value)),
            );
        });
    }

    /**
     * Esta funcion se encarga de que no haya datos repetidos en los [] que almacena los proveedores que tiene ordenes de compra pendientes.
     */
    async getProveedores() {
        new Promise(async (resolve) => {
            //Esta fraccion de codigo es para que no haya items repetidos en el input.
            let arreglo_auxiliar = [];
            this.proveedores.forEach(p => {
                if (!arreglo_auxiliar.some(pe => pe.Codigo === p.Codigo)) {
                    arreglo_auxiliar.push(p);
                }
                this.proveedores = arreglo_auxiliar;
            });

            resolve(true);
        })
    }

    /**
     * Esta funcion es la encargada de obtener los datos de las ordenes de compra y que no se repitan.
     * @param idproveedor Cuando queremos obtener solo las ordenes de compra de un proveedor utilicamos su id para filtrar.
     */
    async getOrdenCompras(idproveedor?: number) {

        if (idproveedor) {
            this.oc_pendientes = this.orden_compras.filter(oc => oc.idproveedor === idproveedor);
        } else {
            this.oc_pendientes = this.orden_compras;
        }

        let arreglo_auxiliar = [];
        this.oc_pendientes.forEach(ocp => {
            if (!arreglo_auxiliar.some(oc => oc.idordencompra === ocp.idordencompra)) {
                arreglo_auxiliar.push(ocp);
            }
            this.oc_pendientes = arreglo_auxiliar;
        })
    }

    getTextInput(option) {
        if (option.descripcion) {
            return option.descripcion;
        }
        if (option.deposito) {
            return option.deposito;
        }
        if (option.RazonSocial) {
            return option.RazonSocial;
        }
        return option;
    }

    /**
     * Esta funcion maneja las cambios de seleccion que puedan tener articulos, unidades de medida u ordenes de compra.
     * @param $event 
    */
    seleccionItem($event) {
        this.isUpdating = true;
        // Si se ha seleccionado un proveedor explcitamente, actualiza el campo del proveedor
        if ($event.Codigo) {
            this.getOrdenCompras($event.Codigo); // Buscamos las OC que posee ese proveedor.
            // Actualizamos el campo del proveedor sin emitir eventos
            this.form.get('proveedor').setValue($event, { emitEvent: false });
            // Habilitamos el campo de OC
            this.form.get('oc_pendiente').enable();
        }

        // Si se ha seleccionado una OC, actualiza el campo de OC pendiente
        if ($event.idordencompra) {
            let oc = this.oc_pendientes.find(prov => prov.idordencompra === $event.idordencompra);
            this.form.get('oc_pendiente').setValue(oc, { emitEvent: false });
            let noExisteProveedor = (this.form.get('proveedor').value === "") || (this.form.get('proveedor').value.Codigo != $event.idproveedor); //Si el campo proveedor es igual a vacio  el proveedor es distinto.
            if (noExisteProveedor) {
                let proveedor = this.proveedores.find(p => p.Codigo === oc.idproveedor);
                this.form.get('proveedor').setValue(proveedor, { emitEvent: false });
                this.getOrdenCompras(proveedor.Codigo);
            };

        }
        this.isUpdating = false;
    }

    async getItems(idremito): Promise<any> {
        return new Promise(async (resolve) => {
            const items = await firstValueFrom(this._remitosService.getItems(idremito));
            this.mov_art_list = items.list;
            resolve(this.mov_art_list);
        })
    }

    /**
     * Esta funcion aade un nuevo item a la grilla dentro del saveComponent.
     */
    addMovimiento() {
        if (this.form.get('oc_pendiente').value != '') {
            let orden_compra_value = this.form.get('oc_pendiente').value.idordencompra;
            let esEdicion = this.remito ? true : false;

            // Validar que se haya seleccionado un deposito vlido
            if (!this.remito || (this.remito && this.form.get('deposito').value !== this.remito.descripcion_deposito)) {
                const depositoSeleccionado = this.form.get('deposito').value;
                const depositoExistente = this.depositos.find(deposito => deposito.iddeposito === depositoSeleccionado.iddeposito);
                if (!depositoExistente) {
                    return this.alert.warning('El deposito seleccionado no existe. Por favor, seleccione un deposito vlido.');
                }
            }

            this.dialogo
                .open(AddArticuloComponent, {
                    panelClass: 'ayuda-dialog',
                    data: {
                        orden_compra: orden_compra_value,
                        movimientos: this.mov_art_list,
                        movimientos_eliminados: this.mov_art_list_deleted,
                        esEdicion: esEdicion
                    }
                })
                .afterClosed()
                .subscribe({
                    next: (res) => {
                        if (res) {
                            let movimiento_articulo = {    //Encapsulo los datos que me interesan mostrar en la grilla.
                                idarticulo: res.articulo.idarticulo,
                                cantidad: res.cantidad,
                                cantidad_pendiente: res.cantidad_pendiente,  //Cantidad pendiente restante de ese articulo con esa um...
                                idunidadmedida: res.unidad_medida,
                                idordencompraitem: res.articulo.idordencompraitem,
                                descripcion_articulo: res.articulo.descripcion,
                                descripcion_um: res.unidadmedida_descripcion,
                                codigo_interno: res.articulo.codigo_interno,
                            }
                            this.mov_art_list = [...this.mov_art_list, movimiento_articulo];
                            this.dataSource = new MatTableDataSource(this.mov_art_list); //Actualizo la tabla

                            // Desabilito el campo proveedor, OC y Deposito
                            if (this.mov_art_list.length != 0) {
                                this.form.get('proveedor').disable();
                                this.form.get('oc_pendiente').disable();
                                this.form.get('deposito').disable();
                            }

                            this.alert.success("Artculo aadido con xito")
                        }
                    },
                });
        } else {
            this.alert.warning("Seleccione una orden de compra");
        }
    }

    /**
     * El resultado de esta operacin es un nuevo 
     * array que contiene todos los movimientos de la lista original, 
     * excepto el artculo que se desea eliminar.
     * @param movimiento_articulo 
     */
    deleteMovimiento(movimiento_articulo) {
        this.mov_art_list.forEach((mov) => {
            if (mov.idarticulo === movimiento_articulo.idarticulo && mov.idunidadmedida === movimiento_articulo.idunidadmedida) {
                mov.cantidad_pendiente = Number(mov.cantidad_pendiente) + Number(mov.cantidad);
            }
        });

        this.mov_art_list_deleted = this.mov_art_list;
        this.mov_art_list = this.mov_art_list.filter((item) => item !== movimiento_articulo);
        this.dataSource = new MatTableDataSource(this.mov_art_list);

        // Habilito el campo de OC y Deposito si la lista de movimientos est vaca
        if (this.mov_art_list.length === 0) {
            this.form.get('oc_pendiente').enable();
            this.form.get('deposito').enable();
        }

        this.alert.success("Artculo eliminado con xito")
    }

    /**
    * Funcion para limpiar campos
    */
    limpiarCasillero(input) {
        this.form.get(input).setValue('');
        if (input == "proveedor") {
            this.form.get('oc_pendiente').setValue('');
            this.getOrdenCompras();
        }
    }

    onSubmit() {
        // Verificar la longitud de las observaciones
        if (!this.isPalabraMaxLengthReached()) {
            return this.alert.error(`La longitud mxima de observaciones es de ${this.maxPalabras} caracteres.`);
        }

        if (!this.form.valid) {
            return this.alert.error('Revise Los Datos Ingresados');
        } else {
            // Validar que se haya seleccionado un proveedor vlido solo si se est agregando un nuevo remito o se cambi el proveedor
            if (!this.remito || (this.remito && this.form.get('proveedor').value !== this.remito.descripcion_proveedor)) {
                const proveedorSeleccionado = this.form.get('proveedor').value;
                const proveedorExistente = this.proveedores.find(proveedor => proveedor.Codigo === proveedorSeleccionado.Codigo);
                if (!proveedorExistente) {
                    return this.alert.error('El proveedor seleccionado no existe. Por favor, seleccione un proveedor vlido.');
                }
            }

            let movimientos = this.mov_art_list.map(objeto => ({   //Encapsulo solamente los datos que utilizar de los articulos...
                idordencompraitem: objeto.idordencompraitem,
                idarticulo: objeto.idarticulo,
                cantidad: objeto.cantidad,
                idunidadmedida: objeto.idunidadmedida,
            }))

            if (movimientos.length === 0) {
                return this.alert.warning('Debe ingresar un articulo al remito')
            }

            const fecha = this.form.get('fecha').value;
            const hora = this.form.get('hora').value;

            // Se formatea la fecha nuevamente al formato deseado (YYYY-MM-DD)
            const fecha_formateada = this.datePipe.transform(fecha, 'yyyy-MM-dd');

            // Se concatena a ambas fecha y hora
            const fecha_hora = `${fecha_formateada} ${hora}`;

            const payload = {
                remito: {
                    idusuario: this.user.id,
                    fecha_hora: fecha_hora,
                    iddeposito: this.form.get('deposito').value.iddeposito,
                    idordencompra: this.form.get('oc_pendiente').value.idordencompra,
                    observaciones: this.form.get('observaciones').value,
                },
                movimientos: movimientos
            }

            if (this.remito?.idremito) {
                this._remitosService.updateRemito(payload, this.remito.idremito)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.editadoExitosamente.emit();
                                this.dialogRef.close(data);
                            }
                        }
                    });
            } else {
                this._remitosService.saveRemito(payload)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.creadoExitosamente.emit();
                                this.dialogRef.close(data);
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}