import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject } from 'rxjs';
import { IRemito } from '../models/remito';
import { AuthService } from '@core/auth/auth.service';
import { Response } from '@shared/models/response-with-token';

@Injectable()
export class RemitosService {
    constructor(private http: HttpClient, private _authService: AuthService) { }
    /**
     * Funcion en el Servicio 
     * @param remito 
     * @returns Crea un item nuevo
     */
    saveRemito(remito: IRemito): Observable<IRemito> {
        return this.http.post<IRemito>(`${environment.stock.remitos}`, remito);
    }

    /**
     * Funcion en el servicio
     * @param remito
     * @returns Item actualizado actualizado
     */
    updateRemito(remito: IRemito, idremito: number): Observable<IRemito> {
        return this.http.put<IRemito>(`${environment.stock.remitos}` + '/' + idremito, remito);
    }

    /**
     * Funcion en el servicio
     * @param idremito 
     * @returns Elimina un item
     */
    deleteRemito(idremito: number): Observable<IRemito> {
        return this.http.delete<IRemito>(`${environment.stock.remitos}` + '/' + idremito);
    }

    getDepositos(option?) {
        let f = '?';
        f += !option ? 'filter[activo]=1' : '';
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.depositos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getProveedores(option?) {
        let f = '?sort=RazonSocial';
        f += !option ? 'filter[activo]=1' : '';
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.administracion.proveedores}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp
                })
            });
        return subject.asObservable();
    }

    getArticulosDepositos(deposito_origen: number) {
        let f = `?filter[deposito]=${deposito_origen}`
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.view_stock_articulo_depositos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getUnidadesDeMedidaArticulos(idarticulo) {
        let f = `?filter[idarticulo]=${idarticulo}`
        f += `&expand=um`
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.articulo_unidad_medidas}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getItems(idremito: number) {
        let f = `?filter[idremito]=${idremito}`;
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.view_stock_movimientos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }

    getUsuarios() {
        let f = `?sort=asc`;
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.login.view_usuarios}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }

    getItemsOcPendiente(option?) {
        let f = `?`;
        f += option ? `filter[idordencompra]=${option}&` : '';
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.view_items_oc_pendientes}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }
}